.signUp{
  background: #D5E9FF; 
  height: 100vh;
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap : 50px ; 
}

.main{
  display: flex;
  flex-wrap: wrap-reverse;
  justify-content: space-evenly;
  align-items: center;

}

.img {
  max-width: 650px;
  max-height: 450px;
}

@media (max-width: 601px) {
  .img {
    width: 200px;
    height: 200px;
  }
}